
.selected-file{
    max-height: 80vh;
}
.selected-file .wrapper{
    min-width: 300px;
}
.selected-file svg{
    cursor: pointer;
    transition: fill .5s, transform .5s;
}
.selected-file svg:hover{
    fill: var(--light--text)
}

.selected-file .file{
    max-height: 65vh;
    overflow: hidden;
    overflow-y: auto;
    min-height: fit-content;
    position: relative;
}

.selected-file .file::-webkit-scrollbar{
    width: .8rem;
}
.selected-file .file::-webkit-scrollbar-thumb{
    background-color: #666;
    border-radius: .5rem;
}
.file::-webkit-scrollbar-track{
    border-radius: .5rem;
    background-color: #ccc;
}


.selected-file .file canvas {
    width: 100%;
    object-fit: fill;
}

.selected-file .post-editor span input[type=range]{
    width: 100%;
}

.selected-file  span.active svg{
    transform: scale(1.5);
    fill: rgb(6, 190, 190);
}

