.light-mode .user__profile{
    background-color: rgb(214, 214, 214);
}

/* cover image */

.profile .cover__image{
    width: 100%;
    max-height: 250px;
    overflow: hidden;
}
.profile .cover__image img{
    object-fit:cover !important;
    aspect-ratio: 1.99;
    cursor: pointer;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
}

/* profile image */
.profile .profile__avatar{
    top: 100%;
    left: 60px;
    width: 150px;
    height: 150px;
    border-radius: 100%;
    transform: translateY(-70%);
    cursor: pointer;
    z-index: 1;
}

.profile .profile__avatar img{
    border-radius: 100%;
    border: 1px solid #777;
    overflow: hidden;
    aspect-ratio: 1;
    object-fit: cover !important;
}

.profile__information{
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    box-shadow: 0px 0px 2px rgba(255, 255, 255, .5);
}

.light-mode .profile__information{
    box-shadow: 0px 0px 2px rgba(0, 0, 0, .5);
}
.profile__information .biography{
    font-size: small;
    color: #666;
}

.profile__information .friends{
    font-size: .9rem;
    color: rgb(7, 190, 190);
}
.profile__information .friends button{
    transition: all .5s linear;
}


.profile__information .social > *{
    margin: 0 .5rem;
    transition: all .5s ease-in-out;
}
.profile__information .social > *:hover{
    transition: all .5s ease-in-out;
    color: white !important;
}

/* PROFILE edit buttonS */
.edit__cover_image__btn{
    cursor: pointer;
    transition: all .3s linear;
}
.edit__cover_image__btn:hover{
    transform: scale(1.1);
}

.dark-mode .edit__cover_image__btn svg{
    fill: #eee !important;
}



/* User Experience */

.user__experience .user__type{
    letter-spacing: 2px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
@media (max-width: 720px) {
    #profileActivity{
        display: none;
    }
    #columnOne{
        padding: 0 !important;

    }
}
@media (max-width: 825px) {
    .profile__information .social button{
        font-size: small;
    }
}